import {
  Flex,
  Text,
  Image,
  Spacer,
  Input,
  Divider,
  Stack,
  Center,
  useDisclosure,
  Textarea,
  useToast,
  Button,
} from "@chakra-ui/react";
import { FC } from "react";
import { useState } from "react";
import { useAddReviewMutation, useGetOfferReviewsQuery } from "../../redux/rtkQuery/api";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import ModalComponent from "../ModalComponent";
import moment from "moment";
import { useNavigate } from "react-router-dom";
import { GLOBAL_PREFIX } from "../../constants";
import { OrderDataProps } from "../../redux/OrderSlice";
const StarRatingComponent = require("react-star-rating-component");

interface MyDealsProps {
  name: string;
  price: number;
  token: string;
  quantity: number;
  date: string;
  expired: boolean;
  image: string;
  rest: OrderDataProps;
}
const MyDeals: FC<MyDealsProps> = ({
  name,
  price,
  token,
  quantity,
  date,
  expired,
  image,
  rest,
}): JSX.Element => {
  const { root, bodyText, headText } = useStyles();
  let navigate = useNavigate();

  const toast = useToast();

  const {
    isOpen: feedbackReviewIsOpen,
    onOpen: feedbackReviewOnOpen,
    onClose: feedbackReviewOnClose,
  } = useDisclosure();

  const [addReview] = useAddReviewMutation();

  const [rating, setRating] = useState<number | null>(null);

  const [addReviewLoading, setAddReviewLoading] = useState<boolean>(false);
  const [reviewText, setReviewText] = useState<string>("");
  const [reviewTitle, setReviewTitle] = useState<string>("");

  const { data: allReviews, refetch: refetchReviews } = useGetOfferReviewsQuery(
    { offerId: rest?.offerId },
    { refetchOnMountOrArgChange: true, skip: !token }
  );

  // console.log(rest);

  async function AddReviewToOffer() {
    if (!reviewTitle) {
      MeProtocolToast(toast, "Please input all fields", 2000);
      return;
    }

    if (reviewText.split(" ").length < 5) {
      MeProtocolToast(toast, "At least five characters required", 2000);
      return;
    }
    if (!rating) {
      MeProtocolToast(toast, "Please select a rating", 2000);
      return;
    }
    setAddReviewLoading(true);
    await addReview({
      title: reviewTitle,
      review: reviewText,
      offer_id: Number(rest?.offerId),
      rating,
    })
      .then((res: any) => {
        if (res?.data?.status === 200) {
          refetchReviews();
          MeProtocolToast(toast, "Review submitted", 5000, "bottom-right");
          feedbackReviewOnClose();
          setRating(null);
          setReviewText("");
          setReviewTitle("");
        }
      })
      .catch((e: any | unknown) => {
        console.log(e, "AddReviewToOfferErr");
      })
      .finally(() => {
        setAddReviewLoading(false);
      });
  }

  return (
    <Stack>
      <Flex {...root} flexDir="row" align={"center"}>
        {/* ------------------------IMAGE---------------------------------- */}
        <Flex w="30%" h={"70%"} flexDir="row" align={"center"} justify="space-between">
          <Flex w="100px" h="100px" flexDir="column">
            <Image objectFit={"cover"} src={image} />
          </Flex>
          <Spacer />
          <Flex flexDir="column" w="70%" h="100px">
            <Text {...headText} noOfLines={1}>
              {name}
            </Text>
            <Spacer />
            <Text {...bodyText}>{token}</Text>
            {/* <Text {...bodyText}>{price.toLocaleString()} USD</Text> */}
            {/* <Text {...bodyText}>Quantity: {quantity}</Text> */}
            <Text {...bodyText}>Purchased: {moment(rest?.updatedAt).fromNow()}</Text>
          </Flex>
        </Flex>
        {/* ------------------------------------------------EXPIRY---------------------------------------------- */}
        {expired && (
          <Flex w="30%" h={"70%"} flexDir={"column"} align="flex-end">
            <Text {...bodyText} color="dark">
              Expires
            </Text>
            <Text {...bodyText}>{moment(rest?.updatedAt).format("DD MMM YYYY")}</Text>
          </Flex>
        )}
        {/* ------------------------VIEW---------------------------------- */}
        <Flex w="30%" h={"70%"} align="flex-end" flexDir={"column"}>
          <Center
            {...bodyText}
            as={Button}
            _hover={{
              bg: "dark",
            }}
            color={"light"}
            w={"35%"}
            h={"30%"}
            isDisabled={!rest?.coupon}
            bg="dark"
            onClick={() => {
              navigate(`${GLOBAL_PREFIX}/dashboard_my_deals_details`, {
                state: { ...rest },
              });
            }}
          >
            View details
          </Center>
          <Text
            as="button"
            {...bodyText}
            opacity={rest?.couponId ? 1 : 0.4}
            onClick={() => {
              if (rest.couponId) feedbackReviewOnOpen();
            }}
          >
            {expired ? "View voucher" : "Leave Review"}
          </Text>
          <Text as={rest?.isRedeemed ? "text" : "button"} {...bodyText}>
            {rest?.isRedeemed ? "Redeemed" : `Status: ${rest.status}`}
          </Text>
          <Text as="button" {...bodyText}>
            Buy it again
          </Text>
        </Flex>
      </Flex>
      <Divider />

      {/* ----------------------------------------LEAVE REVIEW MODAL-------------------------------------- */}

      <ModalComponent
        isOpen={feedbackReviewIsOpen}
        onClose={feedbackReviewOnClose}
        closeOnOverlayClick={true}
      >
        <Text mb={"24px"}>Leave review</Text>
        <Divider />
        <Flex mt={"16px"} mb={"16px"} flexDir={"column"} justify="center" alignItems={"center"}>
          <Text>My overall rating</Text>
          <Flex>
            <StarRatingComponent
              name="rate2"
              starColor="#000000"
              emptyStarColor="#999999"
              starCount={5}
              value={rating}
              onStarClick={(value: any) => {
                setRating(value);
              }}
            />
          </Flex>
        </Flex>
        <Divider />
        <Flex mt="20px" flexDir={"column"}>
          <Flex w="100%">
            <Input
              variant={"outline"}
              borderRadius={"0px"}
              value={reviewTitle}
              onChange={({ target }: { target: { value: string } }) => setReviewTitle(target.value)}
              h="46px"
              placeholder="Review headline or summary"
              _placeholder={{ opacity: 1, color: "#AFAFAF" }}
            />
          </Flex>
          <Flex mt={"16px"} w={"100%"} flexDir={"column"}>
            <Textarea
              variant={"outline"}
              borderRadius={"0px"}
              value={reviewText}
              onChange={({ target }: { target: { value: string } }) => setReviewText(target.value)}
              placeholder="Write your review here. It must be at least 5 characters long."
              _placeholder={{ opacity: 1, color: "#AFAFAF" }}
              size="sm"
            />
            {/* ------------------------- ERROR MESSAGE ON THIS SECTION ---------------------- */}
            {/* <Text color={"red"}>Your review must be at least 5 characters in length.</Text> */}
          </Flex>
        </Flex>

        {/* ----------------------------------- SUBMIT BUTTON ------------------------ */}
        <Flex align={"center"} mt="24px">
          <Button
            // as="button"
            isLoading={addReviewLoading}
            w="100%"
            h="48px"
            bg={"black"}
            _hover={{ bg: "dark" }}
            onClick={() => AddReviewToOffer()}
          >
            <Text color={"white"}>Submit</Text>
          </Button>
        </Flex>
        {/* ----------------------------------- CANCEL BUTTON ------------------------ */}
        <Flex align={"center"} mt="10px">
          <Center
            as="button"
            w="100%"
            h="48px"
            border={"1px solid black"}
            onClick={() => {
              setRating(null);
              setReviewText("");
              setReviewTitle("");
              feedbackReviewOnClose();
            }}
          >
            <Text color={"black"}>Cancel</Text>
          </Center>
        </Flex>
      </ModalComponent>
    </Stack>
  );
};

export default MyDeals;

const useStyles = () => {
  return {
    root: {
      w: "100%",
      h: "150px",
      justify: "space-between",
    },
    headText: {
      fontSize: "16px",
    },
    bodyText: {
      fontSize: "12px",
      color: "#999999",
    },
  };
};
